<template>
    <div style="width:100%;">
        <div class="header_items">
            <div class="company_name">润通保险经纪</div>
            <div v-for="(item,index) in headerItem"
                 :key="index"
                 class="item_move"
                 :class="{item_moveUp:item.itemUpActive,item_moveDown: item.itemDownActive}"
                 @mouseover="itemForceON(index)"
                 @click="pageTurn(item.path)"
            >
                <div class="header_item1">{{item.name}}</div>
                <div class="header_item2">{{item.name}}</div>
            </div>
        </div>
        <div :class="{child_items:childItemShow}" :style="leftDistance">
            <a class="child_item" v-for="(item,index) in childItem " :key="index" href="javascript:;"
               @click="pageTurn(item.path)">{{item.name}}</a>
        </div>
        <!--                <div class="header_img">-->
        <!--                    <img src="../img/banner1.png" width="100%"/>-->
        <!--                </div>-->

    </div>
</template>
<script>
    export default {
        name: "headerArea",
        data() {
            return {
                leftDistance: "",
                childItemShow: false,
                childItem: [],
                headerItem: [
                    {
                        name: "首页",
                        itemUpActive: false,
                        itemDownActive: false,
                        path: '/',
                    },
                    {
                        name: "关于我们",
                        itemUpActive: false,
                        itemDownActive: false,
                        path: "/aboutUs/leaderSpeech",
                        childItem: [{name: "公司介绍"}, {
                            name: "领导致辞",
                            path: "/aboutUs/leaderSpeech"
                        },
                            {
                                name: "组织机构",
                                path: "/aboutUs/leaderSpeech"
                            },
                            {
                                name: "企业文化",
                                path: "/aboutUs/leaderSpeech"
                            }, {
                                name: "荣耀资质",
                                path: "/aboutUs/leaderSpeech"
                            },]
                    },
                    {
                        name: "资讯中心",
                        itemUpActive: false,
                        itemDownActive: false,
                        childItem: [
                            {
                                name: "政策法规",
                                path: "/infoCenter/policyLow"
                            }, {
                                name: "视频新闻",
                                path: "/infoCenter/videoNews"
                            }, {
                                name: "文体活动"
                            }
                        ]
                    },
                    {
                        name: "公告公示",
                        itemUpActive: false,
                        itemDownActive: false,
                        childItem: [
                            {name: "基本信息公开"},
                            {name: "人保招投标公示", path: '/publicNotices/bidNotices'}
                        ]
                    },

                    {
                        name: "明星产品",
                        itemUpActive: false,
                        itemDownActive: false,
                        path: "/starProduct"

                    },
                    {
                        name: "党建群团",
                        itemUpActive: false,
                        itemDownActive: false,
                        childItem: [
                            {name: "党的建设", path: "/politicalParty/partyConstruction"},
                            {name: "纪检工作"},
                            {name: "创先争优"},
                            {name: "文明创建"},
                            {name: "四风举报"},
                        ]
                    },
                    {
                        name: "风险咨询",
                        itemUpActive: false,
                        itemDownActive: false,
                        path: "/riskConsult"
                    },

                ],

            }
        },
        methods: {
            itemForceON: function (index) {
                this.headerItem[index].itemUpActive = false;
                this.headerItem[index].itemDownActive = true;
                this.childItem = this.headerItem[index].childItem ? this.headerItem[index].childItem : {};
                this.childItemShow = this.headerItem[index].childItem ? true : false;
                this.leftDistance = "padding-left:calc(50% - 195px + " + 85 * index + "px)";
                // console.log(this.leftDistance)
                // console.log(this.childItem)
                for (let i = 0; i < this.headerItem.length; i += 1) {
                    if (i != index) {
                        if (this.headerItem[i].itemDownActive) {
                            this.headerItem[i].itemUpActive = true;
                            this.headerItem[i].itemDownActive = false;
                        }
                    }
                }
            },
            pageTurn: function (path) {
                this.$router.push({path: path})
                console.log(this.$router)
            },

        },


    }
</script>

<style>
    .header_items {
        width: 100%;
        height: 85px;
        display: flex;
        justify-content: center;
        overflow: hidden;
        z-index: 200;
        position: absolute;
        top: 0px;
        background-color: white;
    }

    .child_items {
        /*width:100px;*/
        height: 55px;
        background-color: #7cc7ff;
        opacity: 0.5;
        position: absolute;
        top: 85px;
        right: 0;
        left: 0;
        z-index: 100;
        animation: dropDown 0.3s;
        -webkit-animation: dropDown 0.3s;
        animation-timing-function: ease-in;
        -webkit-animation-timing-function: ease-in;
        animation-fill-modeforwards: forwards;
        -webkit-animation-fill-modeforwards: forwards;
    }

    .child_item {
        display: inline-block;
        text-decoration: none;
        height: 55px;
        line-height: 55px;
        padding: 0 10px;
    }

    .header_img {
        width: 100vw;
    }

    .header_item1 {
        width: 85px;
        height: 85px;
        min-width: 85px;
        line-height: 85px;
        text-align: center;
        font-family: MicrosoftYaHei;
        background-color: #4c9cff;
        color: #fff;
        border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px;
    }

    .header_item2 {
        width: 85px;
        height: 85px;
        min-width: 85px;
        line-height: 85px;
        text-align: center;
        font-family: MicrosoftYaHei;
        font-size: 18px;
        color: #707384;

    }

    .company_name {
        width: 200px;
        height: 85px;
        min-width: 200px;
        line-height: 85px;
        text-align: center;
        margin-right: 20px;
        font-family: FZHZGBJW--GB1-0;
        font-size: 30px;
        color: #4c9cff;
    }

    .item_move {
        position: relative;
        top: -85px
    }

    .item_moveUp {
        top: -85px;
        animation: moveUp 0.5s;
        -webkit-animation: moveUp 0.5s;
        animation-timing-function: ease-in;
        -webkit-animation-timing-function: ease-in;
        animation-fill-modeforwards: forwards;
        -webkit-animation-fill-modeforwards: forwards;

    }

    .item_moveDown {
        top: 0px;
        animation: moveDown 0.5s;
        -webkit-animation: moveDown 0.5s;
        animation-timing-function: ease-in;
        -webkit-animation-timing-function: ease-in;
        animation-fill-modeforwards: forwards;
        -webkit-animation-fill-modeforwards: forwards;
    }

    @keyframes moveDown {
        0% {
            top: -85px;
        }
        100% {
            top: 0px;
        }
    }

    @-webkit-keyframes moveDown {
        0% {
            top: -85px;
        }
        100% {
            top: 0px;
        }
    }

    @keyframes moveUp {
        0% {
            top: 0px;
        }
        100% {
            top: -85px;
        }
    }

    @-webkit-keyframes moveUp {
        0% {
            top: 0px;
        }
        100% {
            top: -85px;
        }
    }

    @keyframes dropDown {
        0% {
            top: 40px;
            opacity: 0.1;
        }
        100% {
            top: 85px;
            opacity: 0.5;
        }
    }

    @-webkit-keyframes dropDown {
        0% {
            top: 40px;
            opacity: 0.1;
        }
        100% {
            top: 85px;
            opacity: 0.5;
        }
    }
</style>



